html {
  width: 100%;
  height: 100vh;
}
body {
  width: 100%;
  height: 100%;
}
#root {
  width: 100%;
  height: 100%;
}
.App {
  width: 100%;
  height: 100%;
  position: relative;
}

.map__container {
  width: 100%;
  height: 100%;
}





.scroll-bar {
  padding-left: 6px;
}

.scroll-bar::-webkit-scrollbar {
  width: 6px;
}

.scroll-bar::-webkit-scrollbar-thumb {
  border-radius: 3px;
  background-color: rgba(0, 0, 0, 0.1);
}




